import { useState, useEffect } from 'react'
import { withRouter, Link } from 'react-router-dom'
// import storeData from './config/storeData'
import { Modal } from 'react-bootstrap'

import CartList from './cartlist/CartList'

function S1DoLog(props) {
  const { icon_cross, icon_minus, icon_add } = props
  const {
    cartList,
    HandleAmount,
    Add_Minus,
    setOrderDetail,
    orderDetail,
    memberData,
    orderDetail2,
    setOrderDetail2,
    myAlert,
    storeData,
    cityList,
  } = props

  const printTag = memberData.coupon

  //  
  const [tags, setTags] = useState(orderDetail.tag)
  // 
  const [tagState, setTagState] = useState(orderDetail.discount)

  // selectstate
  const [delivery, setDelivery] = useState(orderDetail.getWay)
  const [pay, setPay] = useState(orderDetail.payWay)
  const [receipt, setReceipt] = useState(orderDetail.receipt)

  //modal > select  state
  const [btnName, setBtnName] = useState(
    orderDetail2.store
      ? orderDetail2.city +
          '-' +
          orderDetail2.block +
          ' ' +
          orderDetail2.store +
          ''
      : ''
  )
  const [city, setCity] = useState(orderDetail2.city ? orderDetail2.city : '0')
  const [block, setBlock] = useState(
    orderDetail2.block ? orderDetail2.block : '0'
  )
  const [store, setStore] = useState(
    orderDetail2.store ? orderDetail2.store : '0'
  )
  const [blockList, setBlockList] = useState([])
  const [storeList, setStoreList] = useState([])

  // 
  const CalcTprice = (disCount) => {
    let Tprice = 0
    cartList.forEach((item) => {
      Tprice += item.quantity * item.price
    })
    if (disCount) {
      return Math.round(Tprice * disCount)
    } else {
      return Math.round(Tprice)
    }
  }

  // TAG
  const ChooseTag = (id) => {
    const tagList = [...tags]
    const index = tagList.findIndex((item) => item.id === id)
    const newTagList = tagList.map((v, i) => {
      if (i !== index) {
        v.picked = false
      } else {
        v.picked = !v.picked
        if (v.picked) {
          setTagState(v.discount)
        } else {
          setTagState(1)
        }
      }

      return v
    })
    setTags(newTagList)
    setOrderDetail({ tag: tags })
  }

  //BTN CLICKED
  const NextStep = () => {
    const detail = {
      tag: tags,
      Tprice: CalcTprice(),
      discount: tagState,
      getWay: delivery,
      payWay: pay,
      receipt: receipt,
      setted: true,
    }
    setOrderDetail(detail)
    if (delivery !== '') {
      setOrderDetail2({
        name: '',
        phone: '',
        city: '',
        addNum: '',
        address: '',
        store: '',
        block: '',
      })
    } else {
      if (store === '0') {
        myAlert('Error', '', 'error', false, 1600)
        return
      }
    }
    props.history.push('/order/step1/step2')
  }

  const [show, setShow] = useState(false)
  const handleClose = () => setShow(false)
  const handleShow = () => setShow(true)
  const handleStore = () => {
    if (delivery === '') {
      if (city === '0' || block === '0' || store === '0') {
        setBtnName('')
        return
      }
      const temp = { ...orderDetail2 }
      temp.store = store
      temp.city = city
      temp.address = storeData[city][block].filter(
        (item) => item.store === store
      )[0].address
      temp.block = block
      setOrderDetail2(temp)
      setBtnName(city + '-' + block + ' ' + store + '')
    }
  }

  useEffect(() => {}, [tags])

  useEffect(() => {
    if (delivery === '') {
      setPay('')
    } else {
      setPay('')
    }
  }, [delivery])

  useEffect(() => {
    if (show) {
      if (city !== '0') {
        setBlockList(Object.keys(storeData[city]))
      } else {
        setBlockList([])
      }
    }
  }, [city])

  useEffect(() => {
    if (show) {
      if (block !== '0') {
        const tempList = []
        storeData[city][block].forEach((v) => {
          tempList.push(v.store)
        })
        setStoreList(tempList)
      } else {
        setStoreList([])
      }
    }
  }, [block])

  return (
    <>
      <div className="order-step-banner mt-2 mb-5">
        <img
          src="/myreact_gp/img/orders/B_STEP1.svg"
          className="mx-auto"
          alt=""
        />
      </div>

      <div className="d-flex align-items-end">
        <h1 className="col-6 pl-0 text-left mb-0"></h1>
        <h2 className="col-2 text-left mb-0"></h2>
        <h2 className="col-1 mb-0">&nbsp;</h2>
        <h2 className="col-3 text-center mb-0"></h2>
      </div>

      <hr className="myhr1" />

      {/*   */}

      <div className="d-flex flex-column cart-div">
        {cartList.map((v, i) => {
          return (
            <CartList
              key={i}
              v={v}
              i={i}
              HandleAmount={HandleAmount}
              Add_Minus={Add_Minus}
              icon_cross={icon_cross}
              icon_minus={icon_minus}
              icon_add={icon_add}
            ></CartList>
          )
        })}
      </div>

      <hr className="myhr2" />
      <div className="d-flex justify-content-between align-items-center my-4">
        <div className="form-group">
          <textarea
            className="form-control order-note"
            id="order-note"
            name="order-note"
            rows="8"
            placeholder="
          
          
          
          "
          ></textarea>
        </div>

        <div className="d-flex flex-column align-items-end">
          <div className="d-flex my-2">
            <h3></h3>
            <h3 className="order-price-label">{CalcTprice()} TWD</h3>
          </div>
          <div className="d-flex mt-2">
            <h3></h3>
            <h3 className="order-price-label">0 TWD</h3>
          </div>

          <hr className="calc-hr" />

          <div className="d-flex align-items-center my-2">
            <h3 className="my-0"></h3>
            <div className="badge-group">
              {printTag.map((v, i) => {
                if (v === '') {
                  return (
                    <Link
                      key={i}
                      onClick={(e) => {
                        e.preventDefault()
                        ChooseTag(1)
                      }}
                      className={`badge badge-cuppon ${
                        orderDetail.tag[0].picked ? 'active' : ''
                      }`}
                      to="/"
                    >
                      9
                    </Link>
                  )
                }
                if (v === '') {
                  return (
                    <Link
                      key={i}
                      onClick={(e) => {
                        e.preventDefault()
                        ChooseTag(2)
                      }}
                      className={`badge badge-cuppon ${
                        orderDetail.tag[1].picked ? 'active' : ''
                      }`}
                      to="/"
                    >
                      8
                    </Link>
                  )
                }
                if (v === '') {
                  return (
                    <Link
                      key={i}
                      onClick={(e) => {
                        e.preventDefault()
                        ChooseTag(3)
                      }}
                      className={`badge badge-cuppon ${
                        orderDetail.tag[2].picked ? 'active' : ''
                      }`}
                      to="/"
                    >
                      7
                    </Link>
                  )
                }
              })}
            </div>
          </div>

          <div className="d-flex mt-3">
            <h3></h3>
            <h3 className="order-price-label">
              {`${CalcTprice(tagState)} `}
              TWD
            </h3>
          </div>

          <div className="form-group form-check order-checkbox my-2">
            <label className="form-check-label">
              <input type="checkbox" className="form-check-input" />
              /JU4T(/)
            </label>
          </div>
        </div>
      </div>

      <hr className="stephr v1" />

      <form className="order-deliver-form ml-5">
        <div className="form-group">
          <label className="order-select-label"></label>
          <select
            className="btn primary-select ml-4 mt-2"
            value={delivery}
            onChange={(e) => {
              setDelivery(e.target.value)
            }}
          >
            <option value=""></option>

            <option value=""></option>
          </select>
          {delivery === '' ? (
            <h6 className="mt-3"></h6>
          ) : (
            <>
              <small className="form-text text-bold mt-4">
                {' '}
                7-11
              </small>
              <button
                className="btn primary-btn md ml-4 mt-2"
                onClick={(e) => {
                  e.preventDefault()
                  handleShow()
                }}
              >
                {btnName}
              </button>
            </>
          )}
        </div>
        {delivery === '' ? (
          ''
        ) : (
          <div className="form-group my-5">
            <label className="order-select-label"></label>
            <select
              className="btn primary-select ml-4 mt-2"
              value={pay}
              onChange={(e) => {
                setPay(e.target.value)
              }}
            >
              <option value=""></option>
            </select>
          </div>
        )}

        <div className="form-group">
          <label className="order-select-label"></label>
          <select
            className="btn primary-select ml-4 mt-2"
            value={receipt}
            onChange={(e) => {
              setReceipt(e.target.value)
            }}
          >
            <option value=""></option>
            <option value="-">-</option>
            <option value="-">-</option>
            <option value=""></option>
          </select>
          <small className="form-text text-muted mt-4">
            
          </small>
        </div>
      </form>

      <div className="order-shop-note">
        <h4 style={{ fontWeight: 500 }}></h4>
        <ul>
          <li>
            2
          </li>
          <li>
            (24)24!
          </li>
          <li>
            JU4TJU4T
            
          </li>
        </ul>
      </div>

      <div className="text-center my-5">
        <button
          className="btn important-btn md mx-auto"
          onClick={() => {
            NextStep()
          }}
        >
          
        </button>

        <small className="mt-3 d-block text-muted text-center">
           Evolutive Labs' Sales
          and Refunds Policy
        </small>

        <Modal
          className="addtocart-success-modal"
          show={show}
          onHide={handleClose}
          backdrop="static"
          keyboard={false}
        >
          <Modal.Header closeButton></Modal.Header>
          <Modal.Body className="text-center">
            <select
              name="city"
              id="city"
              className="btn primary-select my-2"
              value={city}
              onChange={(e) => {
                setCity(e.target.value)
                setBlock('0')
                setStore('0')
              }}
            >
              <option value="0"></option>
              {cityList
                ? cityList.map((v, i) => {
                    return (
                      <option key={i} value={v}>
                        {v}
                      </option>
                    )
                  })
                : ''}
            </select>
            <select
              name="block"
              id="block"
              className="btn primary-select my-2"
              value={block}
              onChange={(e) => {
                setBlock(e.target.value)
                setStore('0')
              }}
            >
              <option value="0"></option>
              {blockList
                ? blockList.map((v, i) => {
                    return (
                      <option key={i} value={v}>
                        {v}
                      </option>
                    )
                  })
                : ''}
            </select>
            <select
              name="store"
              id="store"
              className="btn primary-select my-2"
              value={store}
              onChange={(e) => {
                setStore(e.target.value)
              }}
            >
              <option value="0"></option>
              {storeList
                ? storeList.map((v, i) => {
                    return (
                      <option key={i} value={v}>
                        {v} &nbsp;&nbsp;&nbsp;
                      </option>
                    )
                  })
                : ''}
            </select>
          </Modal.Body>
          <Modal.Footer className="d-flex justify-content-center">
            <button
              className="btn important-btn md mb-4"
              onClick={() => {
                handleClose()
                handleStore()
              }}
            >
              
            </button>
          </Modal.Footer>
        </Modal>
      </div>
    </>
  )
}

export default withRouter(S1DoLog)
